import { notification } from 'antd';

export const success = (title, message) => {
  notification.success({
    message: title || 'DEU CERTO',
    description: message || 'Ao realizada com sucesso.'
  });
}

export const error = (err) => {
  if(err.response && err.response.status === 503) err503();
  else if(err.response && err.response.status === 401) err401();
  else errGeneral();
}

const err503 = () => {
  notification.error({
    message: 'ERRO 503',
    description: 'Servidor est inacessvel no momento, tente carregar a pgina novamente, se o erro persistir, entre em contato com o tcnico.'
  });
}

const err401 = () => {
  notification.error({
    message: 'ERRO 401',
    description: 'Erro ao realizar a ao. Voc no tem permisso para isto.'
  });
}

const errGeneral = () => {
  notification.error({
    message: 'ERRO',
    description: 'Erro ao realizar esta ao. Mas no entendemos direito o que houve, tente carregar a pgina novamente, se o erro persistir, entre em contato com o tcnico.'
  });
}